import React, { createContext, useContext } from "react";
import { useResolvedContent } from "../hooks/useResolvedContent";


import { useCMS } from "../../contexts/CMSContext";

const ResolvedContentContext = createContext<any>(null);

export const ResolvedContentProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const { displayData } = useCMS();

  return (
    <ResolvedContentContext.Provider value={displayData}>
      {children}
    </ResolvedContentContext.Provider>
  );
};

export const useResolved = () => {
  const ctx = useContext(ResolvedContentContext);
  if (!ctx) {
    throw new Error("useResolved must be used inside ResolvedContentProvider");
  }
  return ctx;
};