import React, { useState, useEffect } from 'react';
import '../templates/DefaultLegalStyles.css';

const CookieBanner: React.FC = () => {
    const [isVisible, setIsVisible] = useState(false);

    useEffect(() => {
        // Check if user has already consented
        const consent = localStorage.getItem('cookieConsent');
        if (!consent) {
            setIsVisible(true);
        }
    }, []);

    const handleAccept = () => {
        localStorage.setItem('cookieConsent', 'true');
        setIsVisible(false);
    };

    if (!isVisible) return null;

    return (
        <div className="cookie-banner" id="cookie-consent-banner">
            <div className="cookie-text">
                We use cookies to improve your experience and analyze site traffic.
                By clicking "Accept", you agree to our use of cookies as described in our
                <a href="/cookies" style={{ color: '#E6B422', textDecoration: 'underline', marginLeft: '4px' }}>Cookie Policy</a>.
            </div>
            <div className="cookie-actions">
                <button className="cookie-btn-accept" onClick={handleAccept}>
                    Accept
                </button>
                <a href="/cookies" className="cookie-btn-manage">
                    Manage cookies
                </a>
            </div>
        </div>
    );
};

export default CookieBanner;
