import React from "react";
import { RiDoubleQuotesL, RiDoubleQuotesR } from "react-icons/ri";
import "./programsec.css";
import { useCMS } from "../../contexts/CMSContext";

const KhongGianThien: React.FC = () => {
  const { displayData } = useCMS();
  const c = displayData.pages.page3.khongGianThienContent;

  if (!c) return null;

  return (
    <section className="kgt-section">
      <div className="kgt-container">


        {/* LEFT IMAGE */}
        <div className="kgt-image" style={{ order: (c.imagePosition || 'left') === 'right' ? 2 : 1 }}>
          <img src={c.image.src} alt={c.image.alt} onError={(e) => (e.currentTarget.style.display = 'none')} />
        </div>

        {/* RIGHT CONTENT */}
        <div className="kgt-content" style={{ order: (c.imagePosition || 'left') === 'right' ? 1 : 2 }}>

          <h2 className="kgt-title" data-aos="fade-up">
            {c.title}
          </h2>
          <div className="kgt-underline" />

          <p className="kgt-quote" data-aos="fade-up" data-aos-delay="100">
            <RiDoubleQuotesL className="quote-left" />
            {c.quote}
            <RiDoubleQuotesR className="quote-right" />
          </p>

          {/* CARDS */}
          <div className="kgt-cards">
            {c.cards.map((card, index) => (
              <div
                key={card.id}
                className="kgt-card"
                data-aos="fade-up"
                data-aos-delay={200 + index * 100}
              >
                <div className="kgt-thumb">
                  {card.image ? (
                    <img
                      src={card.image}
                      alt={card.title}
                      className="kgt-thumb-img"
                      onError={(e) => (e.currentTarget.style.display = 'none')}
                    />
                  ) : (
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      viewBox="0 0 24 24"
                      fill="none"
                      stroke="currentColor"
                      strokeWidth="1"
                    >
                      <rect x="3" y="3" width="18" height="18" rx="1" ry="1" />
                      <circle cx="8.5" cy="8.5" r="1.5" />
                      <polyline points="21 15 16 10 5 21" />
                    </svg>
                  )}
                </div>

                <h4>{card.title}</h4>
                <p>{card.subtitle}</p>
              </div>
            ))}
          </div>

        </div>
      </div>
    </section>
  );
};

export default KhongGianThien;
