import React, { useState, useEffect } from 'react';
import { useCMS } from '../../contexts/CMSContext';
import { NavItem } from '../../contexts/types';
import './header3.css'

interface HeaderProps {
  activeNav?: string;
}

const Header3: React.FC<HeaderProps> = ({ activeNav = 'home' }) => {
  const { displayData } = useCMS();
  const headerContent = displayData.global.headerContent;
  const headerData = displayData.pages.page3.header;

  const [isScrolled, setIsScrolled] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [activeDropdown, setActiveDropdown] = useState<string | null>(null);
  const [mobileDropdown, setMobileDropdown] = useState<string | null>(null);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const toggleDropdown = (dropdown: string) => {
    setActiveDropdown(activeDropdown === dropdown ? null : dropdown);
  };

  const toggleMobileDropdown = (dropdown: string) => {
    setMobileDropdown(mobileDropdown === dropdown ? null : dropdown);
  };

  const closeMobileMenu = () => {
    setMobileMenuOpen(false);
    setActiveDropdown(null);
    setMobileDropdown(null);
  };

  return (
    <>
      <header className={`headerpage3-header ${isScrolled ? 'headerpage3-scrolled' : ''}`}>
        <div className="headerpage3-header-container">
          <div className="headerpage3-logo-container" onClick={() => window.location.href = '/'}>
            <div className="headerpage3-logo-icon">
              <img src={headerContent.logoImage} alt="SBU Logo" />
            </div>
            <div className="headerpage3-logo-text">
              <div className="headerpage3-logo-vietnamese">{headerContent.logoVietnamese}</div>
              <div className="headerpage3-logo-english">{headerContent.logoEnglish}</div>
            </div>
          </div>

          <nav className={`headerpage3-desktop-nav ${mobileMenuOpen ? 'headerpage3-mobile-open' : ''}`}>
            <ul className="headerpage3-nav-list">
              {headerContent.navigationItems.map((navItem: NavItem) => (
                navItem.isDropdown ? (
                  <li
                    key={navItem.id}
                    className={`headerpage3-nav-item headerpage3-dropdown ${activeDropdown === navItem.id ? 'headerpage3-active' : ''}`}
                    onMouseEnter={() => setActiveDropdown(navItem.id)}
                    onMouseLeave={() => setActiveDropdown(null)}
                  >
                    <button
                      className="headerpage3-dropdown-toggle"
                      onClick={() => toggleDropdown(navItem.id)}
                      aria-expanded={activeDropdown === navItem.id}
                    >
                      {navItem.label}
                      <svg className="headerpage3-dropdown-arrow" width="10" height="6" viewBox="0 0 10 6" fill="none">
                        <path d="M1 1L5 5L9 1" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" />
                      </svg>
                    </button>
                    {navItem.dropdownItems && (
                      <div className="headerpage3-dropdown-menu">
                        {navItem.dropdownItems.map((dropItem: any) => (
                          <a key={dropItem.id} href={dropItem.href} className="headerpage3-dropdown-item">
                            {dropItem.label}
                          </a>
                        ))}
                      </div>
                    )}
                  </li>
                ) : (
                  <li key={navItem.id} className={`headerpage3-nav-item ${activeNav === navItem.id ? 'headerpage3-active' : ''}`}>
                    <a href={navItem.href} className="headerpage3-nav-link">{navItem.label}</a>
                  </li>
                )
              ))}
            </ul>
          </nav>

          <div className="headerpage3-mobile-controls">
            <button className="headerpage3-mobile-search-toggle" aria-label="Search">
              <svg width="37" height="37" viewBox="0 0 24 24" fill="none">
                <circle cx="11" cy="11" r="8" stroke="currentColor" strokeWidth="2" />
                <path d="m21 21-4.35-4.35" stroke="currentColor" strokeWidth="2" />
              </svg>
            </button>
            <button
              className="headerpage3-mobile-menu-toggle"
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
              aria-label="Toggle menu"
            >
              <span className={`headerpage3-menu-line ${mobileMenuOpen ? 'headerpage3-open' : ''}`}></span>
              <span className={`headerpage3-menu-line ${mobileMenuOpen ? 'headerpage3-open' : ''}`}></span>
              <span className={`headerpage3-menu-line ${mobileMenuOpen ? 'headerpage3-open' : ''}`}></span>
            </button>
          </div>
        </div>
      </header>

      {/* Hero Section - Only render if published */}
      {headerData.published && (
        <section
          className="headerpage3-hero"
          style={{
            '--desktop-bg': `url('${headerData.backgroundImage}')`,
            '--mobile-bg': `url('${headerData.mobileBackgroundImage || headerData.backgroundImage}')`
          } as React.CSSProperties}
        >
          <div className="headerpage3-hero-overlay"></div>
          <div className="headerpage3-hero-content">
            <h1 className="headerpage3-hero-title">
              <span className="headerpage3-title-desktop">
                {headerData.title.split('\n').map((line: string, i: number) => (
                  <span key={i} className={`headerpage3-title-line-${i + 1}`}>{line}<br /></span>
                ))}
              </span>
              <span className="headerpage3-title-mobile">{headerData.mobileTitle || headerData.title.split('\n')[0]}</span>
            </h1>
          </div>
        </section>
      )}

      {/* Mobile Menu Overlay */}
      <div className={`headerpage3-mobile-menu-overlay ${mobileMenuOpen ? 'headerpage3-active' : ''}`} onClick={closeMobileMenu}></div>
      <div className={`headerpage3-mobile-menu ${mobileMenuOpen ? 'headerpage3-active' : ''}`}>
        <div className="headerpage3-mobile-menu-header">
          <div className="headerpage3-mobile-logo">
            <div className="headerpage3-logo-icon">
              <img src={headerContent.logoImage} alt="SBU Logo" />
            </div>
            <div className="headerpage3-mobile-logo-text">
              <div>{headerContent.logoVietnamese}</div>
              <div>{headerContent.logoEnglish}</div>
            </div>
          </div>
          <button className="headerpage3-mobile-menu-close" onClick={closeMobileMenu} aria-label="Close menu">
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
              <path d="M18 6L6 18" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
              <path d="M6 6L18 18" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
            </svg>
          </button>
        </div>
        <ul className="headerpage3-mobile-nav-list">
          {headerContent.navigationItems.map((navItem: NavItem) => (
            navItem.isDropdown ? (
              <li key={navItem.id} className={`headerpage3-mobile-nav-item headerpage3-mobile-dropdown ${mobileDropdown === navItem.id ? 'headerpage3-active' : ''}`}>
                <button
                  className="headerpage3-mobile-dropdown-toggle"
                  onClick={() => toggleMobileDropdown(navItem.id)}
                  aria-expanded={mobileDropdown === navItem.id}
                >
                  {navItem.label}
                  <svg className="headerpage3-dropdown-arrow" width="10" height="6" viewBox="0 0 10 6" fill="none">
                    <path d="M1 1L5 5L9 1" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" />
                  </svg>
                </button>
                <div className="headerpage3-mobile-dropdown-menu">
                  {navItem.dropdownItems?.map((dropItem: any) => (
                    <a key={dropItem.id} href={dropItem.href} className="headerpage3-mobile-dropdown-item" onClick={closeMobileMenu}>
                      {dropItem.label}
                    </a>
                  ))}
                </div>
              </li>
            ) : (
              <li key={navItem.id} className="headerpage3-mobile-nav-item">
                <a href={navItem.href} className="headerpage3-mobile-nav-link" onClick={closeMobileMenu}>{navItem.label}</a>
              </li>
            )
          ))}
        </ul>
      </div>
    </>
  );
};

export default Header3;