import React, { useState, useEffect } from "react";
import { FaYoutube, FaFacebook, FaArrowUp } from "react-icons/fa";
import "./Footer.css";
import { useCMS } from "../../contexts/CMSContext";
import { DraftPreviewBar } from "../DraftPreviewBar";
import CookieBanner from "./CookieBanner";

const Footer: React.FC = () => {
  const { displayData, hasDraftChanges } = useCMS();
  const footer = displayData.global.footerContent;

  const [showScrollTop, setShowScrollTop] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setShowScrollTop(window.scrollY > 800);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  if (!footer) return null;

  return (
    <>
      <DraftPreviewBar hasDraftChanges={hasDraftChanges} />
      <footer
        className="footer"
        style={{ '--footer-bg': `url(${footer.backgroundImage})` } as React.CSSProperties}
      >
        <div className="footer-overlay" />

        <div className="footer-inner">
          {/* Header row */}
          <div className="footer-header">
            <div className="footer-title-group">
              <h2 className="footer-title desktop-title" data-aos="fade-up">
                {footer.titleDesktop}
              </h2>
              <h2 className="footer-title mobile-title" data-aos="fade-up">
                {footer.titleMobile.split("\n").map((line, i) => (
                  <React.Fragment key={i}>
                    {line}
                    <br />
                  </React.Fragment>
                ))}
              </h2>
            </div>

            <div
              className="footer-logo"
              data-aos="fade-up"
              data-aos-delay="100"
            >
              <img src={footer.logoImage} alt={footer.logoAlt} />
            </div>
          </div>

          {/* Content */}
          <div className="footer-content">
            {/* Contact */}
            <div className="footer-col" data-aos="fade-up" data-aos-delay="200">
              <h3>{footer.contact.title}</h3>
              <p>
                {footer.contact.address.split("\n").map((line, i) => (
                  <React.Fragment key={i}>
                    {line}
                    <br />
                  </React.Fragment>
                ))}
              </p>
              <p>{footer.contact.phone}</p>
              <p>{footer.contact.email}</p>
            </div>

            {/* Quick links */}
            <div className="footer-col" data-aos="fade-up" data-aos-delay="300">
              <h3>{footer.quickLinks.title}</h3>
              <ul>
                {footer.quickLinks.items.map(item => (
                  <li key={item.id}>
                    {item.href ? <a href={item.href}>{item.label}</a> : item.label}
                  </li>
                ))}
              </ul>
            </div>

            {/* Social */}
            <div
              className="footer-col footer-social"
              data-aos="fade-up"
              data-aos-delay="400"
            >
            
              <div className="social-icons">
                {footer.social.youtube && (
                  <a
                    href={footer.social.youtube}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="social-item"
                  >
                    <FaYoutube />
                    <span>YouTube</span>
                  </a>
                )}
                {footer.social.facebook && (
                  <a
                    href={footer.social.facebook}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="social-item"
                  >
                    <FaFacebook />
                    <span>Facebook</span>
                  </a>
                )}
              </div>
            </div>
          </div>

          {/* Bottom */}
          <div className="footer-bottom" data-aos="fade-up" data-aos-delay="500">
            <p className="footer-copyright">
              ¬© {new Date().getFullYear()} {footer.copyright}
            </p>
            <p className="footer-promotion">
              {footer.creditText}{" "}
              <a
                href={footer.creditLink}
                target="_blank"
                rel="noopener noreferrer"
              >
                {footer.creditLabel}
              </a>
            </p>
          </div>
        </div>

        {/* Scroll to Top */}
        {showScrollTop && (
          <button
            className="scroll-to-top"
            onClick={scrollToTop}
            aria-label="Scroll to top"
          >
            <FaArrowUp />
          </button>
        )}
        <CookieBanner />
      </footer>
    </>
  );
};

export default Footer;
