import React, { useState } from "react";
import "./form.css";
import { useResolved } from '../../ppm/util/ResolvedContentContext';
import FormRenderer from '../../components/FormBuilder/FormRenderer';
import { useFormData } from '../../hooks/useFormData';
import "../../components/FormBuilder/FormRenderer.css";

const GhiDanhThienForm: React.FC = () => {
  const c = useResolved().pages.page3.ghiDanhThienContent;
  const { forms } = useFormData();

  // Live lookup
  const masterForm = forms.find(f => f.id === c.formId);
  const formToRender = masterForm || c.form;

  const [birthDate, setBirthDate] = useState("");
  const [dateError, setDateError] = useState("");

  if (!c) return null;

  const validateDate = (value: string) => {
    const r = /^(\d{1,2})\/(\d{1,2})\/(\d{4})$/;
    const match = value.match(r);

    if (!match) {
      setDateError("ƒê·ªãnh d·∫°ng kh√¥ng h·ª£p l·ªá (dd/mm/yyyy)");
      return;
    }

    const [, d, m, y] = match;
    const date = new Date(+y, +m - 1, +d);
    const today = new Date();

    if (
      date.getDate() !== +d ||
      date.getMonth() !== +m - 1 ||
      date.getFullYear() !== +y
    ) {
      setDateError("Ng√†y kh√¥ng h·ª£p l·ªá");
      return;
    }

    if (date > today) {
      setDateError("Ng√†y sinh kh√¥ng th·ªÉ trong t∆∞∆°ng lai");
      return;
    }

    setDateError("");
  };

  return (
    <section id="GHI-DANH-THI·ªÄN">

      {/* DESKTOP */}
      <section className="from-register-section from-register-desktop">
        <div className="from-register-container">

          <div className="from-register-image">
            <img src={c.desktopImage} alt="B·ªì ƒë·ªÅ" />
          </div>

          <div className="from-register-form">
            <h2 className="from-register-title">{c.title}</h2>
            <div className="from-register-underline" />

            <form>
              {formToRender ? (
                <div className="custom-form-container">
                  <FormRenderer form={formToRender} />
                </div>
              ) : (
                <div className="from-form-grid">

                  <div className="from-form-group">
                    <label>{c.fields.fullName.label}</label>
                    <input placeholder={c.fields.fullName.placeholder} />
                  </div>

                  <div className="from-form-group">
                    <label>{c.fields.dharmaName.label}</label>
                    <input placeholder={c.fields.dharmaName.placeholder} />
                  </div>

                  <div className="from-form-group">
                    <label>{c.fields.birthDate.label}</label>
                    <input
                      placeholder={c.fields.birthDate.placeholder}
                      value={birthDate}
                      onChange={e => {
                        setBirthDate(e.target.value);
                        validateDate(e.target.value);
                      }}
                      className={dateError ? "error" : ""}
                    />
                    {dateError && (
                      <span className="error-message">{dateError}</span>
                    )}
                  </div>

                  <div className="from-form-group">
                    <label>{c.fields.gender.label}</label>
                    <div className="from-radio-group">
                      <label>
                        <input type="radio" name="gender" />
                        {c.fields.gender.male}
                      </label>
                      <label>
                        <input type="radio" name="gender" />
                        {c.fields.gender.female}
                      </label>
                    </div>
                  </div>

                  <div className="from-form-group">
                    <label>{c.fields.email.label}</label>
                    <input placeholder={c.fields.email.placeholder} />
                  </div>

                  <div className="from-form-group">
                    <label>{c.fields.phone.label}</label>
                    <input placeholder={c.fields.phone.placeholder} />
                  </div>

                  <div className="from-form-group">
                    <label>{c.fields.program.label}</label>
                    <select>
                      {c.fields.program.options.map(o => (
                        <option key={o.id}>{o.label}</option>
                      ))}
                    </select>
                  </div>

                  <div className="from-form-group">
                    <label>{c.fields.attendance.label}</label>
                    <select>
                      {c.fields.attendance.options.map(o => (
                        <option key={o.id}>{o.label}</option>
                      ))}
                    </select>
                  </div>

                </div>
              )}

              <div className="from-form-submit">
                <button type="submit">{c.submitLabel}</button>
              </div>
            </form>
          </div>
        </div>
      </section>

      {/* MOBILE */}
      <div className="from-register-mobile">
        <div className="from-register-mobile-header">
          <h2 className="from-register-title">{c.title}</h2>
          <div className="from-register-underline" />
        </div>

        <div className="from-register-mobile-content">
          <form>
            {formToRender ? (
              <div className="custom-form-container">
                <FormRenderer form={formToRender} />
              </div>
            ) : (
              <div className="from-form-grid">

                <div className="from-form-group">
                  <label>{c.fields.fullName.label}</label>
                  <input placeholder={c.fields.fullName.placeholder} />
                </div>

                <div className="from-form-group">
                  <label>{c.fields.dharmaName.label}</label>
                  <input placeholder={c.fields.dharmaName.placeholder} />
                </div>

                <div className="from-form-group">
                  <label>{c.fields.birthDate.label}</label>
                  <input
                    placeholder={c.fields.birthDate.placeholder}
                    value={birthDate}
                    onChange={e => {
                      setBirthDate(e.target.value);
                      validateDate(e.target.value);
                    }}
                    className={dateError ? "error" : ""}
                  />
                  {dateError && (
                    <span className="error-message">{dateError}</span>
                  )}
                </div>

                <div className="from-form-group">
                  <label>{c.fields.gender.label}</label>
                  <div className="from-radio-group">
                    <label>
                      <input type="radio" name="gender-mob" />
                      {c.fields.gender.male}
                    </label>
                    <label>
                      <input type="radio" name="gender-mob" />
                      {c.fields.gender.female}
                    </label>
                  </div>
                </div>

                <div className="from-form-group">
                  <label>{c.fields.email.label}</label>
                  <input placeholder={c.fields.email.placeholder} />
                </div>

                <div className="from-form-group">
                  <label>{c.fields.phone.label}</label>
                  <input placeholder={c.fields.phone.placeholder} />
                </div>

                <div className="from-form-group">
                  <label>{c.fields.program.label}</label>
                  <select>
                    {c.fields.program.options.map(o => (
                      <option key={o.id}>{o.label}</option>
                    ))}
                  </select>
                </div>

                <div className="from-form-group">
                  <label>{c.fields.attendance.label}</label>
                  <select>
                    {c.fields.attendance.options.map(o => (
                      <option key={o.id}>{o.label}</option>
                    ))}
                  </select>
                </div>

              </div>
            )}

            <div className="from-form-submit">
              <button type="submit">{c.submitLabel}</button>
            </div>
          </form>
        </div>
      </div>
    </section>
  );
};

export default GhiDanhThienForm;
